.container {
    display: flex;
    padding: 20px;
    justify-content: space-around;
}

.gameBoard {
    width: 512px;
    height: 512px;
    display: flex;
    flex-wrap: wrap;
}

.gameBoard img {
    width: 64px;
    height: 64px;
    background-color: rgba(0, 0, 0, .4);
    border-radius: 25%;
}

.score_section {
    display: flex;
    align-items: center;
    justify-content: center;
}

.score {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    padding-right: 20px;
    background: linear-gradient(to left,  #f80000 0%,#f98d00 50%, #ffb301 51%,#edda00 100%);
    line-height: 1;
    letter-spacing: 1px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: .6px black;
}

.reset_btn {
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: black;
    font-weight: 600;
    padding: 10px;
    border-radius: 15px 15px 15px 15px;
    background: linear-gradient(to bottom,  #404177 15%,#531a54 30%, #ac4ab6 51%,#d8d7e9 100%);
    text-shadow: 0px 5px 10px #d8d7e9;
}

.reset_btn:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 10px #d8d7e9;
}

@media only screen and (max-width: 580px) {
    .container {
        padding-top: 50px;
    }

    .gameBoard {
        width: 280px;
        height: 280px;
        display: flex;
        flex-wrap: wrap;
    }
    
    .gameBoard img {
        width: 35px;
        height: 35px;
    }
}